<template>
  <AppCardLayout>
    <AppCardGroup>
      <AppProductsGrid
        v-if="orderData"
        :items="orderData.products"
        mode="user-ro"
      />
    </AppCardGroup>

    <AppCardGroup>
      <OrderFormRo
        v-if="orderData"
        :data="(orderData as any)"
      />
    </AppCardGroup>

    <AppCardGroup>
      <AppOrderPublicStatusForm
        v-if="orderPublicStatusDisplayData"
        :id="orderId"
        :data="orderPublicStatusDisplayData"
        :readonly="true"
      />
    </AppCardGroup>

    <AppCardGroup>
      <OrderDiscussion
        :order-id="orderId"
        mode="customer"
      />
    </AppCardGroup>
  </AppCardLayout>
</template>

<script setup lang="ts">
import AppCardGroup from '@/components/markup/AppCardGroup.vue';
import AppOrderPublicStatusForm, {
  OrderPublicStatusDisplayData,
} from '@/components/app/AppOrderPublicStatusForm.vue';
import AppCardLayout from '@/components/markup/AppCardLayout.vue';
import OrderDiscussion from './components/order-discussion.vue';
import OrderFormRo from '../user/components/order-form-ro.vue';
import {useRoute} from 'vue-router';
import {ref} from 'vue';
import {
  OrderListService,
  UserOrderDisplayItem,
} from '@/services/order-list.service';

const route = useRoute();
const service = useService(OrderListService);
const queryParams = route.query;
const orderIdRaw = queryParams['orderId'];
const orderId = Number(Array.isArray(orderIdRaw) ? orderIdRaw[0] : orderIdRaw);
const orderData = ref(null as null | UserOrderDisplayItem);
const orderPublicStatusDisplayData = ref(
  null as null | OrderPublicStatusDisplayData,
);
onMounted(async () => {
  const rows = await service.getUserOrders(orderId);
  const value = rows[0];
  orderData.value = value as any;

  const {
    orderStatusTypeId,
    productsPriceToPay,
    totalPriceToPay,
    shippingPriceToPay,
  } = value;

  orderPublicStatusDisplayData.value = {
    orderStatusTypeId,
    productsPriceToPay,
    totalPriceToPay,
    shippingPriceToPay,
  };
});
</script>
